<template>
    <div class="container">
        <div class="pages">
            <div class="mainwrap">
                <div class="panel-hd">
                    <div class="emphasis-name"><strong>JOIN US</strong><em>|</em><span></span></div>
                </div>
                <div class="join-us-wrap">
                    <div class="jtl-01"></div>
                    <div class="jtl-02"></div>
                    <div class="jtl-item" :class="[{'open': openItem01}]">
                        <div class="antig" @click="handleOpen(1)"></div>
                        <div class="job-head">
                            <div class="zh-name"></div>
                            <div class="eng-name">Illustrator</div>
                            <div class="comp-feature"> | <span class="num">2</span></div>
                        </div>
                        <div class="job-desc">
                            <div class="advantage">
                            <br />
                            1<br />
                            2<br />
                            3
                            </div>
                            <div class="advantage">
                            <br />
                            12-3<br />
                            2AIPhotoshop<br />
                            3<br />
                            4
                            </div>
                        </div>
                    </div>
                    <div class="jtl-item" :class="[{'open': openItem02}]">
                        <div class="antig" @click="handleOpen(2)"></div>
                        <div class="job-head">
                            <div class="zh-name"></div>
                            <div class="eng-name">Senior Designer</div>
                            <div class="comp-feature">  | <span class="num">2</span></div>
                        </div>
                        <div class="job-desc">
                            <div class="advantage">
                            <br />
                            1<br />
                            2<br />
                            3
                            </div>
                            <div class="advantage">
                            <br />
                            12-3<br />
                            2AIPhotoshop<br />
                            3<br />
                            4
                            </div>
                        </div>
                    </div>
                    <div class="jtl-item" :class="[{'open': openItem03}]">
                        <div class="antig" @click="handleOpen(3)"></div>
                        <div class="job-head">
                            <div class="zh-name"></div>
                            <div class="eng-name">Marketing Planning</div>
                            <div class="comp-feature"> | <span class="num">1</span></div>
                        </div>
                        <div class="job-desc">
                            <div class="advantage">
                            <br />
                            1<br />
                            2<br />
                            3
                            </div>
                            <div class="advantage">
                            <br />
                            12-3<br />
                            2AIPhotoshop<br />
                            3<br />
                            4
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="sidebar">
                <cnode-sidebar></cnode-sidebar>
            </div>
        </div>
    </div>
</template>

<script>
import cnodeSidebar from '@/components/Sidebar'

export default {
    name: 'Joinus',
    components: {
        cnodeSidebar
    },
    data() {
        return {
            openItem01: true,
            openItem02: false,
            openItem03: false,
            loading: false
        };
    },
    methods: {
        handleOpen(vc){
            let ary = [1, 2, 3];
            for (let i = 0; i < 3; i++) {
                if (vc === ary[i]){
                    this['openItem0'+ary[i]] = true;
                }else{
                    this['openItem0'+ary[i]] = false;
                }
            }
        }
    }
}
</script>



